import React, { useState, useEffect, Fragment, useCallback } from "react";
import { Typography, Grid, Paper, Box, Button } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../store/actions";
import { useTranslation } from "react-i18next";
import Input from "../../components/Inputs/Input";
import TableSkeleton from "../../components/common/TableSkeleton";
import { isError } from "../../util/generals";
import SaveRoundedIcon from "@material-ui/icons/SaveRounded";
import ButtonSpinner from "../../components/UI/Spinners/ButtonSpinner";
import MoblieInput from "../../components/Inputs/MoblieInput";
import VisibilityIcon from "@material-ui/icons/Visibility";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";

const initialProfileState = (profile) => {
  let state = {
    firstname: {
      value: profile ? profile.firstname : "",
      validity: !!profile.firstname,
    },
    lastname: {
      value: profile ? profile.lastname : "",
      validity: !!profile.lastname,
    },
    email: {
      value: profile ? profile.email : "",
      validity: !!profile.email,
    },
    role: {
      value: profile ? profile.role : "",
      validity: !!profile.role,
    },
    country_code: {
      value: profile ? profile.country_code + "" : "966",
      validity: true,
    },
    mobile: {
      value: profile ? profile.mobile : "",
      validity: !!profile.mobile,
    },
  };
  return state;
};
const initialPasswordState = {
  oldpassword: {
    value: "",
    validity: false,
  },
  password: {
    value: "",
    validity: false,
  },
};
let timeout;
const Settings = () => {
  const profile = useSelector((state) => state.profile);
  const [profileState, setProfileState] = useState({});
  const [passwordState, setPasswordState] = useState(initialPasswordState);
  const [visible, setVisible] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [touched, setTouched] = useState(false);
  const [forceReset, setForseReset] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const handleProfileState = (value, validity, name) => {
    setProfileState((prev) => {
      return {
        ...prev,
        [name]: {
          value: value,
          validity: validity,
        },
      };
    });
  };
  const handlePasswordState = (value, validity, name) => {
    setPasswordState((prev) => {
      return {
        ...prev,
        [name]: {
          value: value,
          validity: validity,
        },
      };
    });
  };
  const fetchProfile = useCallback(async () => {
    try {
      const profile = await dispatch(actions.getProfile());
      setProfileState(initialProfileState(profile));
    } catch (err) {
      dispatch(actions.fireSnake("error", err.message));
    } finally {
      setMounted(true);
    }
  }, [dispatch]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchProfile();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchProfile]);

  const handleSaveProfile = async () => {
    let body = {};

    for (const key in profileState) {
      body = { ...body, [key]: profileState[key].value };
      if (!profileState[key].validity) {
        setTouched(true);
        return dispatch(
          actions.fireSnake("error", t("common:complete_red_input"))
        );
      }
    }

    try {
      const newProfile = await dispatch(actions.updateProfile(body));
      setProfileState(initialProfileState(newProfile));
      dispatch(actions.fireSnake("success", t("common:saved_success")));
      setForseReset(true);
    } catch (err) {
      dispatch(actions.fireSnake("error", err.message));
    }
  };
  const handleChangePassword = async () => {
    let body = {};

    for (const key in passwordState) {
      body = { ...body, [key]: passwordState[key].value };
      if (!passwordState[key].validity) {
        setTouched(true);
        return dispatch(
          actions.fireSnake("error", t("common:complete_red_input"))
        );
      }
    }

    try {
      await dispatch(actions.changePassword(body));
      setPasswordState(initialPasswordState);
      dispatch(actions.fireSnake("success", t("common:pass_reset_success")));
      setForseReset(true);
    } catch (err) {
      dispatch(actions.fireSnake("error", err.message));
    }
  };

  useEffect(() => {
    if (forceReset) {
      setForseReset(false);
      setTouched(false);
    }
  }, [forceReset]);

  return (
    <Fragment>
      <Typography variant="h4" className="py" align="center">
        {t("common:profile_info")}
      </Typography>

      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} col={2} />
        </Paper>
      ) : (
        <Fragment>
          <Grid container spacing={1}>
            {/* GENERAL */}
            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Typography
                  variant="caption"
                  className="py"
                  align="center"
                  component="div"
                >
                  {`${t("common:profile_general")} (${t("settings:role")}: ${t(
                    `settings:${profileState.role.value}`
                  )})`}
                </Typography>
                <Grid container spacing={1}>
                  <Grid item md={6} sm={12} xs={12}>
                    <Input
                      label={t("common:firstname")}
                      initialValue={profileState.firstname.value}
                      name="firstname"
                      getResult={handleProfileState}
                      min={2}
                      max={11}
                      setTouched={touched}
                      serverError={isError(
                        profile.errors && profile.errors.data,
                        `firstname`
                      )}
                    />
                  </Grid>
                  <Grid item md={6} sm={12} xs={12}>
                    <Input
                      label={t("common:lastname")}
                      initialValue={profileState.lastname.value}
                      name="lastname"
                      getResult={handleProfileState}
                      min={2}
                      max={11}
                      setTouched={touched}
                      serverError={isError(
                        profile.errors && profile.errors.data,
                        `lastname`
                      )}
                    />
                  </Grid>
                  <Grid item md={6} sm={12} xs={12}>
                    <Input
                      label={t("common:email")}
                      initialValue={profileState.email.value}
                      name="email"
                      getResult={() => {}}
                      disabled
                      serverError={isError(
                        profile.errors && profile.errors.data,
                        `email`
                      )}
                    />
                  </Grid>
                  <Grid item md={6} sm={12} xs={12}>
                    <MoblieInput
                      reducer={profile}
                      countryCode={profileState.country_code.value}
                      mobile={profileState.mobile.value}
                      onChange={handleProfileState}
                      touched={touched}
                    />
                  </Grid>
                </Grid>
                {/* Save Profile */}
                <Box className="center my-1">
                  <Button
                    variant="outlined"
                    color="primary"
                    size="large"
                    onClick={handleSaveProfile}
                    endIcon={profile.itemLoading && <ButtonSpinner />}
                    startIcon={<SaveRoundedIcon />}
                    disabled={profile.itemLoading}
                    style={{ width: "80%" }}
                  >
                    <Typography variant="h4">{t("common:save")}</Typography>
                  </Button>
                </Box>
              </Paper>
            </Grid>

            {/* CHANGE PASSWORD */}
            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Typography
                  variant="caption"
                  className="xp"
                  align="center"
                  component="div"
                >
                  {t("common:change_password")}
                </Typography>
                {!forceReset && (
                  <Input
                    label={t("common:oldpassword")}
                    name="oldpassword"
                    getResult={handlePasswordState}
                    withIcon={
                      visible ? (
                        <VisibilityIcon
                          color="primary"
                          onClick={() => setVisible(!visible)}
                          className="hoverable-op"
                        />
                      ) : (
                        <VisibilityOffIcon
                          color="disabled"
                          onClick={() => setVisible(!visible)}
                          className="hoverable-op"
                        />
                      )
                    }
                    setTouched={touched}
                    type={visible ? "text" : "password"}
                    required
                    min={5}
                    max={21}
                    margin={4}
                    serverError={isError(
                      profile.errors && profile.errors.data,
                      `oldpassword`
                    )}
                  />
                )}
                {!forceReset && (
                  <Input
                    label={t("common:newpassword")}
                    name="password"
                    getResult={handlePasswordState}
                    withIcon={
                      visible ? (
                        <VisibilityIcon
                          color="primary"
                          onClick={() => setVisible(!visible)}
                          className="hoverable-op"
                        />
                      ) : (
                        <VisibilityOffIcon
                          color="disabled"
                          onClick={() => setVisible(!visible)}
                          className="hoverable-op"
                        />
                      )
                    }
                    setTouched={touched}
                    type={visible ? "text" : "password"}
                    required
                    min={5}
                    max={21}
                    margin={4}
                    serverError={isError(
                      profile.errors && profile.errors.data,
                      `password`
                    )}
                  />
                )}
                {/* Save Password */}
                <Box className="center my-1">
                  <Button
                    variant="outlined"
                    color="primary"
                    size="large"
                    onClick={handleChangePassword}
                    endIcon={profile.itemLoading && <ButtonSpinner />}
                    startIcon={<SaveRoundedIcon />}
                    disabled={profile.itemLoading}
                    style={{ width: "80%" }}
                  >
                    <Typography variant="h4">{t("common:save")}</Typography>
                  </Button>
                </Box>
              </Paper>
            </Grid>
          </Grid>
        </Fragment>
      )}
    </Fragment>
  );
};

export default Settings;
